import React, { Component } from 'react';

export default class Cell extends Component{
    constructor(props){
        super(props);

        this.state = ({
            serialNumber : props.serialNumber,
        });
    }

    clickHandle(e){
        console.log(e);
    }

    render(){
        return(
            <div className="cell" onClick={this.clickHandle.bind(e,this)}>
                {this.state.serialNumber}
            </div>
        );
    }
}